header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 10rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  transition: all 0.5s;
  z-index: 10;

  &.on {
    background: rgba(255, 255, 255, 0.6);
    color: #333;
    backdrop-filter: blur(6px);
  }

  h1 {
    height: 3.5rem;
    font: {
      size: 4rem;
      family: 'VAL';
    }
  }
}

.header {
  &-inner {
    max-width: 1280px;
    min-width: 320px;
    width: 100%;
    height: inherit;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
  }

  &-list {
    display: flex;
    align-items: center;
    gap: 2rem;
    a {
      text-transform: uppercase;
      font: {
        size: 2rem;
        weight: 700;
      }
    }
  }
}
